@charset "UTF-8";
#responsive-tester {
  bottom: 0.5rem;
  display: block;
  font-size: 0.85rem;
  left: 0.5rem;
  padding-left: 1.5rem;
  position: fixed;
}

#responsive-tester span {
  background-color: #999;
  border-radius: 3rem;
  color: rgba(255, 255, 255, 0);
  display: inline-block;
  height: 1rem;
  left: 0;
  line-height: 1;
  margin-top: -0.5rem;
  overflow: hidden;
  position: absolute;
  top: 50%;
  width: 1rem;
  z-index: 10000;
}

#responsive-tester::before, #responsive-tester::after {
  background-color: black;
  color: #FFF;
  display: inline;
  opacity: 0.8;
  padding: 0.15rem;
}

#responsive-tester::before {
  content: 'Base: 0px–';
}

#responsive-tester::after {
  content: "576px";
}

